.App {
	display: flex;
	flex-direction: column;
	height: 100vh;
	margin: 0 auto;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}

	to {
		transform: rotate(360deg);
	}
}

.body {
	display: flex;
	flex-direction: column;
	height: 100vh;
	overflow-y: auto;
	padding: 15px;
}

.row {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 100%;
}

@media only screen and (max-width: 720px) {
	.body {
		padding: 0;
	}

	.row {
		flex-direction: column;
	}
}